import { LOG_TYPE } from "../../../common/constants";
import peact from "../../../core/peact";
import styles from "./action.module.css";

/*
 * type: move
 * ${fromColumnTitle}  ${toColumnTitle}  .
 */

const Action = ({ todoLog }) => {
  const { columnTitle, title, author, type } = todoLog;

  const actionInnerHTML = `
    <div class="${styles.icon}"></div>
    <div class="content">
        <p class="author">${author}</p>
        <p class="content">
            <strong>${columnTitle}</strong> <strong>${title}</strong>
            <strong>${LOG_TYPE[type]}</strong>.
        </p>
        <p class="${styles.time}">1 </p>
    </div>
  `;
  return peact.createElement({
    tag: "div",
    className: styles.action,
    child: [actionInnerHTML],
  });
};

export default Action;
